dary-light: #34d399;\n    --color-secondary-dark: #059669;\n    --color-accent: #f59e0b;\n    --color-accent-light: #fbbf24;\n    --color-accent-dark: #d97706;\n\n    // Feedback colors\n    --color-success: #10b981;\n    --color-error: #ef4444;\n    --color-warning: #f59e0b;\n    --color-info: #3b82f6;\n\n    // Component base tokens\n    --btn-border-radius: 8px;\n    --card-border-radius: 12px;\n    --input-border-radius: 8px;\n\n    // Animation tokens\n    --transition-fast: 150ms ease;\n    --transition-base: 250ms ease;\n    --transition-slow: 350ms ease;\n\n    // Spacing scale\n    --spacing-xs: 0.25rem;\n    --spacing-sm: 0.5rem;\n    --spacing-md: 1rem;\n    --spacing-lg: 1.5rem;\n    --spacing-xl: 2rem;\n    --spacing-2xl: 3rem;\n    --spacing-3xl: 4rem;\n}\n\n// Sports theme variant\n[data-theme=\"sports\"] {\n    --color-primary: #e11d48;\n    --color-primary-light: #f43f5e;\n    --color-primary-dark: #be123c;\n    --color-secondary: #0284c7;\n    --color-secondary-light: #0ea5e9;\n    --color-secondary-dark: #0369a1;\n    --color-accent: #eab308;\n    --color-accent-light: #facc15;\n    --color-accent-dark: #ca8a04;\n\n    // Component-specific overrides\n    --btn-border-radius: 4px;\n    --card-border-radius: 8px;\n}\n\n// Wellness theme variant\n[data-theme=\"wellness\"] {\n    --color-primary: #0d9488;\n    --color-primary-light: #14b8a6;\n    --color-primary-dark: #0f766e;\n    --color-secondary: #8b5cf6;\n    --color-secondary-light: #a78bfa;\n    --color-secondary-dark: #7c3aed;\n    --color-accent: #ec4899;\n    --color-accent-light: #f472b6;\n    --color-accent-dark: #db2777;\n\n    // Component-specific overrides\n    --btn-border-radius: 24px;\n    --card-border-radius: 16px;\n}\n\n// Gym theme variant\n[data-theme=\"gym\"] {\n    --color-primary: #000000;\n    --color-primary-light: #1f2937;\n    --color-primary-dark: #000000;\n    --color-secondary: #fbbf24;\n    --color-secondary-light: #fcd34d;\n    --color-secondary-dark: #f59e0b;\n   