ent Colors */\n  --color-button-primary: var(--color-primary);\n  --color-button-secondary: var(--color-white, #ffffff);\n  --color-button-primary-text: var(--color-text-inverse);\n  --color-button-secondary-text: var(--color-primary);\n  \n  /* Gradient Colors */\n  --color-gradient-from: var(--color-primary);\n  --color-gradient-to: var(--color-secondary);\n  \n  /* Shadow Colors */\n  --color-shadow: rgba(0, 0, 0, 0.1);\n  --color-shadow-dark: rgba(0, 0, 0, 0.2);\n  \n  /* Focus Ring */\n  --color-focus-ring: var(--color-primary);\n\n  /**\n   * Gym Theme Overrides\n   */\n  &[data-theme=\"gym\"] {\n    --color-primary: var(--color-violet-500, #8b5cf6);\n    --color-secondary: var(--color-purple-500, #a855f7);\n    --color-accent: var(--color-purple-400, #c084fc);\n    \n    --color-text-accent: var(--color-primary);\n    \n    --color-button-primary: var(--color-primary);\n    --color-button-secondary-text: var(--color-primary);\n    \n    --color-gradient-from: var(--color-primary);\n    --color-gradient-to: var(--color-secondary);\n    \n    --color-focus-ring: var(--color-primary);\n  }\n\n  /**\n   * Sports Theme Overrides\n   */\n  &[data-theme=\"sports\"] {\n    --color-primary: var(--color-cyan-500, #06b6d4);\n    --color-secondary: var(--color-blue-500, #3b82f6);\n    --color-accent: var(--color-cyan-400, #22d3ee);\n    \n    --color-text-accent: var(--color-primary);\n    \n    --color-button-primary: var(--color-primary);\n    --color-button-secondary-text: var(--color-primary);\n    \n    --color-gradient-from: var(--color-primary);\n    --color-gradient-to: var(--color-secondary);\n    \n    --color-focus-ring: var(--color-primary);\n  }\n\n  /**\n   * Wellness Theme Overrides\n   */\n  &[data-theme=\"wellness\"] {\n    --color-primary: var(--color-teal-500, #14b8a6);\n    --color-secondary: var(--color-emerald-500, #10b981);\n    --color-accent: var(--color-teal-400, #2dd4bf);\n    \n    --color-text-accent: var(--color-primary);\n    \n    --color-button-primary: var(--color-primary);\n    --color-button-secondary-text: var(--color-primary);\n    \n    --color-gradient-from: var(--color-primary);\n    --color-gradient-to: var(--color-secondary);\n    \n    --color-focus-ring: var(--color-primary);\n  }\n\n  /**\n   * Nutrition Theme Overrides\n   */\n  &[data-theme=\"nutrition\"] {\n    --color-primary: var(--color-amber-500, #f59e0b);\n    --color-secondary: var(--color-amber-600, #d97706);\n    --color-accent: var(--color-amber-400, #fbbf24);\n    \n    --color-text-accent: var(--color-primary);\n    \n    --color-button-primary: var(--color-primary);\n    --color-button-secondary-text: var(--color-primary);\n    \n    --color-gradient-from: var(--color-primary);\n    --color-gradient-to: var(--color-secondary);\n    \n    --color-focus-ring: var(--color-primary);\n  }\n} ","/**\n * Button Component Tokens\n *\n * This file defines button-specific tokens structured according to the\n * Theme Token Organization Schema.\n */\n\n:root {\n  /**\n   * Base Button Tokens\n   * Shared across all button variants\n   */\n  \n  /* Sizing */\n  --button-padding-x: var(--spacing-4, 1rem);\n  --button-padding-y: var(--spacing-2, 0.5rem);\n  --button-border-radius: var(--radius-md, 0.375rem);\n  --button-min-width: 6rem;\n  \n  /* Size Variants */\n  --button-font-size-sm: var(--font-size-xs, 0.75rem);\n  --button-font-size-md: var(--font-size-sm, 0.875rem);\n  --button-font-size-lg: var(--font-size-base, 1rem);\n  \n  --button-padding-x-sm: var(--spacing-3, 0.75rem);\n  --button-padding-y-sm: var(--spacing-1, 0.25rem);\n  --button-padding-x-md: var(--button-padding-x, 1rem);\n  --button-padding-y-md: var(--button-padding-y, 0.5rem);\n  --button-padding-x-lg: var(--spacing-5, 1.25rem);\n  --button-padding-y-lg: var(--spacing-3, 0.75rem);\n  \n  /* Typography */\n  --button-font-weight: var(--font-weight-semibold, 600);\n  --button-line-height: 1.5;\n  --button-letter-spacing: 0.025em;\n  \n  /* Effects */\n  --button-transition: 0.3s ease;\n  --button-shadow: var(--shadow-sm, 0 1px 2px rgba(0, 0, 0, 0.05));\n  --button-shadow-hover: var(--shadow-md, 0 4px 6px rgba(0, 0, 0, 0.1));\n  --button-transform-hover: translateY(-1px);\n  --button-transform-active: translateY(1px);\n  \n  /* States */\n  --button-opacity-disabled: 0.65;\n  \n  /* Icon Layout */\n  --button-icon-spacing: var(--spacing-2, 0.5rem);\n  --button-icon-size-sm: 0.875rem;\n  --button-icon-size-md: 1rem;\n  --button-icon-size-lg: 1.25rem;\n  \n  /**\n   * Primary Button Tokens\n   */\n  --button-primary-color: var(--color-button-primary-text, #ffffff);\n  --button-primary-bg: var(--color-button-primary, var(--color-primary));\n  --button-primary-border-color: transparent;\n  --button-primary-border-width: 0;\n  \n  /* Gradient variant */\n  --button-primary-gradient-angle: 90deg;\n  --button-primary-gradient-from: var(--color-gradient-from, var(--color-primary));\n  --button-primary-gradient-to: var(--color-gradient-to, var(--color-secondary));\n  \n  /* Hover state */\n  --button-primary-color-hover: var(--button-primary-color);\n  --button-primary-bg-hover: var(--color-primary-dark, var(--color-primary));\n  \n  /* Focus state */\n  --button-primary-ring-color: var(--color-focus-ring, var(--color-primary));\n  --button-primary-ring-width: 2px;\n  --button-primary-ring-offset: 2px;\n  \n  /**\n   * Secondary Button Tokens\n   */\n  --button-secondary-color: var(--color-button-secondary-text, var(--color-primary));\n  --button-secondary-bg: var(--color-button-secondary, var(--color-white, #ffffff));\n  --button-secondary-border-color: var(--color-primary);\n  --button-secondary-border-width: 1px;\n  \n  /* Hover state */\n  --button-secondary-color-hover: var(--button-secondary-color);\n  --button-secondary-bg-hover: var(--color-gray-50, #f9fafb);\n  --button-secondary-border-color-hover: var(--color-primary-dark, var(--color-primary));\n  \n  /* Focus state */\n  --button-secondary-ring-color: var(--color-focus-ring, var(--color-primary));\n  --button-secondary-ring-width: 2px;\n  --button-secondary-ring-offset: 2px;\n  \n  /**\n   * Tertiary Button Tokens\n   */\n  --button-tertiary-color: var(--color-text, #111827);\n  --button-tertiary-bg: transparent;\n  --button-tertiary-border-color: transparent;\n  --button-tertiary-border-width: 0;\n  \n  /* Hover state */\n  --button-tertiary-color-hover: var(--color-primary);\n  --button-tertiary-bg-hover: var(--color-gray-50, #f9fafb);\n  \n  /* Focus state */\n  --button-tertiary-ring-color: var(--color-focus-ring, var(--color-primary));\n  --button-tertiary-ring-width: 2px;\n  --button-tertiary-ring-offset: 2px;\n  \n  /**\n   * Hero Button Tokens\n   * Specific to HeroButton component\n   */\n  \n  /* Base dimensions */\n  --hero-button-padding-x: var(--button-padding-x-lg, 1.25rem);\n  --hero-button-padding-y: var(--button-padding-y-lg, 0.75rem);\n  --hero-button-radius: var(--radius-full, 9999px);\n  --hero-button-min-width: 210px;\n  --hero-button-font-weight: var(--button-font-weight, 600);\n  \n  /* Size variants */\n  --hero-button-size-sm-padding-x: 2rem;\n  --hero-button-size-sm-padding-y: 0.875rem;\n  --hero-button-size-sm-min-width: 240px;\n  --hero-button-size-sm-font-size: 1rem;\n  \n  --hero-button-size-md-padding-x: 2.5rem;\n  --hero-button-size-md-padding-y: 1rem;\n  --hero-button-size-md-min-width: 280px;\n  --hero-button-size-md-font-size: 1.125rem;\n  \n  --hero-button-size-lg-padding-x: 3rem;\n  --hero-button-size-lg-padding-y: 1.125rem;\n  --hero-button-size-lg-min-width: 320px;\n  --hero-button-size-lg-font-size: 1.25rem;\n  \n  /* Effects and transitions */\n  --hero-button-transition: var(--button-transition);\n  --hero-button-shadow: var(--button-shadow);\n  --hero-button-shadow-hover: var(--shadow-lg, 0 10px 15px -3px rgba(0, 0, 0, 0.1));\n  --hero-button-transform-up: -3px;\n  --hero-button-transform-down: 1px;\n  \n  /* Primary variant */\n  --hero-button-primary-border-width: 2px;\n  --hero-button-primary-border-color: rgba(var(--color-primary-rgb, 163, 230, 53), 0.3);\n  --hero-button-primary-gradient-angle: var(--button-primary-gradient-angle, 90deg);\n  --hero-button-primary-gradient-from: var(--button-primary-gradient-from);\n  --hero-button-primary-gradient-to: var(--button-primary-gradient-to);\n  \n  /* Secondary variant */\n  --hero-button-secondary-bg: rgba(15, 23, 42, 0.8);\n  --hero-button-secondary-color: var(--color-text-inverse, #ffffff);\n  --hero-button-secondary-border-width: 2px;\n  --hero-button-secondary-border-color: rgba(var(--color-primary-rgb, 163, 230, 53), 0.3);\n  --hero-button-secondary-bg-hover: rgba(var(--color-primary-rgb, 163, 230, 53), 0.1);\n}\n\n/**\n * Theme-Specific Button Tokens\n */\n\n/* Gym Theme Button Tokens */\n[data-theme=\"gym\"] {\n  --button-primary-gradient-from: var(--color-violet-500, #8b5cf6);\n  --button-primary-gradient-to: var(--color-purple-500, #a855f7);\n  --hero-button-primary-border-color: rgba(var(--color-violet-rgb, 139, 92, 246), 0.3);\n  --hero-button-secondary-border-color: rgba(var(--color-violet-rgb, 139, 92, 246), 0.3);\n  --hero-button-secondary-bg-hover: rgba(var(--color-violet-rgb, 139, 92, 246), 0.1);\n}\n\n/* Sports Theme Button Tokens */\n[data-theme=\"sports\"] {\n  --button-primary-gradient-from: var(--color-cyan-500, #06b6d4);\n  --button-primary-gradient-to: var(--color-blue-500, #3b82f6);\n  --hero-button-primary-border-color: rgba(var(--color-cyan-rgb, 6, 182, 212), 0.3);\n  --hero-button-secondary-border-color: rgba(var(--color-cyan-rgb, 6, 182, 212), 0.3);\n  --hero-button-secondary-bg-hover: rgba(var(--color-cyan-rgb, 6, 182, 212), 0.1);\n}\n\n/* Wellness Theme Button Tokens */\n[data-theme=\"wellness\"] {\n  --button-primary-gradient-from: var(--color-teal-500, #14b8a6);\n  --button-primary-gradient-to: var(--color-emerald-500, #10b981);\n  --hero-button-primary-border-color: rgba(var(--color-teal-rgb, 20, 184, 166), 0.3);\n  --hero-button-secondary-border-color: rgba(var(--color-teal-rgb, 20, 184, 166), 0.3);\n  --hero-button-secondary-bg-hover: rgba(var(--color-teal-rgb, 20, 184, 166), 0.1);\n}\n\n/* Nutrition Theme Button Tokens */\n[data-theme=\"nutrition\"] {\n  --button-primary-gradient-from: var(--color-amber-500, #f59e0b);\n  --button-primary-gradient-to: var(--color-amber-600, #d97706);\n  --hero-button-primary-border-color: rgba(var(--color-amber-rgb, 245, 158, 11), 0.3);\n  --hero-button-secondary-border-color: rgba(var(--color-amber-rgb, 245, 158, 11), 0.3);\n  --hero-button-secondary-bg-hover: rgba(var(--color-amber-rgb, 245, 158, 11), 0.1);\n} ","/**\n * Theme Selectors\n *\n * This file provides standardized selectors for applying themes to components.\n * It follows the patterns defined in the Theme Architecture Documentation.\n */\n\n/**\n * Theme Root Selectors\n * \n * Pattern: [data-theme=\"theme-name\"]\n * Used to target global theme styles\n */\n\n/* Base Theme (default) */\n:root:not([data-theme]), \n:root[data-theme=\"default\"] {\n  /* Default theme is already defined in root scope */\n}\n\n/* Gym Theme */\n[data-theme=\"gym\"] {\n  /* Gym theme overrides */\n}\n\n/* Sports Theme */\n[data-theme=\"sports\"] {\n  /* Sports theme overrides */\n}\n\n/* Wellness Theme */\n[data-theme=\"wellness\"] {\n  /* Wellness theme overrides */\n}\n\n/* Nutrition Theme */\n[data-theme=\"nutrition\"] {\n  /* Nutrition theme overrides */\n}\n\n/**\n * Component-Level Theme Selectors\n * \n * Pattern: .component { styles }\n *          [data-theme=\"theme-name\"] .component { theme overrides }\n */\n\n/* Standard Component Theming Pattern */\n.component-example {\n  /* Default theme styling using variables */\n  color: var(--component-color);\n  background: var(--component-background);\n  \n  /* Theme-specific overrides */\n  [data-theme=\"gym\"] & {\n    --component-color: var(--color-gym-text);\n    --component-background: var(--color-gym-background);\n  }\n  \n  [data-theme=\"sports\"] & {\n    --component-color: var(--color-sports-text);\n    --component-background: var(--color-sports-background);\n  }\n  \n  [data-theme=\"wellness\"] & {\n    --component-color: var(--color-wellness-text);\n    --component-background: var(--color-wellness-background);\n  }\n  \n  [data-theme=\"nutrition\"] & {\n    --component-color: var(--color-nutrition-text);\n    --component-background: var(--color-nutrition-background);\n  }\n}\n\n/**\n * Mixin-Style Class Selectors for Common Theme Patterns\n */\n\n/* Primary Button Theme Pattern */\n.theme-primary-button {\n  /* Default primary button styling */\n  background: linear-gradient(\n    var(--angle-gradient, 90deg),\n    var(--color-primary),\n    var(--color-secondary)\n  );\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific gradient will be achieved through CSS variables */\n  }\n}\n\n/* Secondary Button Theme Pattern */\n.theme-secondary-button {\n  /* Default secondary button styling */\n  border: 2px solid var(--color-primary);\n  color: var(--color-primary);\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific styles will be achieved through CSS variables */\n  }\n}\n\n/* Card Theme Pattern */\n.theme-card {\n  /* Default card styling */\n  background: var(--card-background);\n  border: 1px solid var(--card-border);\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific styles will be achieved through CSS variables */\n  }\n}\n\n/**\n * Helper Classes for Explicit Theme Application\n * These allow adding theme classes directly to elements\n */\n\n/* Apply specific theme */\n.theme-default {\n  --current-theme: \"default\";\n}\n\n.theme-gym {\n  --current-theme: \"gym\";\n}\n\n.theme-sports {\n  --current-theme: \"sports\";\n}\n\n.theme-wellness {\n  --current-theme: \"wellness\";\n}\n\n.theme-nutrition {\n  --current-theme: \"nutrition\";\n} ","/**\n * FitCopilot Theme System\n *\n * This is the main entry point for the theme system.\n * It imports all theme-related files and provides overarching theme variables.\n */\n\n// Design tokens - raw values\n@use './design-system/colors-next.scss' as colors;\n\n// Semantic tokens - meaningful names mapped to design tokens\n@use './design-system/semantic-tokens.scss' as semantic;\n\n// Component-specific tokens\n@use './design-system/button-tokens.scss' as button;\n\n// Theme selectors and patterns\n@use './design-system/theme-selectors.scss' as selectors;\n\n/**\n * Base Theme Variables\n * \n * These variables are inherited and potentially overridden by specific themes.\n */\n:root {\n  // Core colors (in addition to those defined in colors-next.scss)\n  --color-primary: #8b5cf6; // violet-500\n  --color-primary-light: #c4b5fd; // violet-300\n  --color-primary-dark: #6d28d9; // violet-700\n  --color-primary-rgb: 139, 92, 246; // violet-500 RGB\n  --color-primary-dark-rgb: 109, 40, 217; // violet-700 RGB\n\n  --color-secondary: #3b82f6; // blue-500\n  --color-secondary-light: #93c5fd; // blue-300\n  --color-secondary-dark: #1d4ed8; // blue-700\n  --color-secondary-rgb: 59, 130, 246; // blue-500 RGB\n\n  --color-background: #111827; // gray-900\n  --color-background-light: #1f2937; // gray-800\n  --color-background-dark: #030712; // gray-950\n\n  --color-text: #f9fafb; // gray-50\n  --color-text-muted: #9ca3af; // gray-400\n  --color-text-dark: #111827; // gray-900\n\n  --color-border: #374151; // gray-700\n\n  // Typography\n  --font-family-sans: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, sans-serif;\n  --font-family-heading: var(--font-family-sans);\n\n  // Typography Scale\n  --font-size-xs: 0.75rem; // 12px\n  --font-size-sm: 0.875rem; // 14px\n  --font-size-base: 1rem; // 16px\n  --font-size-lg: 1.125rem; // 18px\n  --font-size-xl: 1.25rem; // 20px\n  --font-size-2xl: 1.5rem; // 24px\n  --font-size-3xl: 1.875rem; // 30px\n  --font-size-4xl: 2.25rem; // 36px\n  --font-size-5xl: 3rem; // 48px\n  --font-size-6xl: 3.75rem; // 60px\n\n  // Font Weights\n  --font-weight-normal: 400;\n  --font-weight-medium: 500;\n  --font-weight-semibold: 600;\n  --font-weight-bold: 700;\n  --font-weight-extrabold: 800;\n\n  // Spacing\n  --spacing-0: 0;\n  --spacing-1: 0.25rem;\n  --spacing-2: 0.5rem;\n  --spacing-3: 0.75rem;\n  --spacing-4: 1rem;\n  --spacing-6: 1.5rem;\n  --spacing-8: 2rem;\n  --spacing-12: 3rem;\n  --spacing-16: 4rem;\n  --spacing-20: 5rem;\n  --spacing-24: 6rem;\n\n  // Border Radius\n  --radius-sm: 0.125rem;\n  --radius-md: 0.25rem;\n  --radius-lg: 0.5rem;\n  --radius-xl: 1rem;\n  --radius-2xl: 1.5rem;\n  --radius-full: 9999px;\n\n  // Shadows\n  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);\n  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);\n  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);\n  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);\n  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);\n  --shadow-inner: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);\n  --shadow-none: 0 0 #0000;\n\n  // Transitions\n  --transition-fast: 150ms;\n  --transition-normal: 250ms;\n  --transition-slow: 350ms;\n  --transition-all: all var(--transition-normal) ease;\n}\n\n/**\n * Theme-Aware Body Class Selectors\n * \n * These provide an alternative way to apply themes using classes\n * rather than data-attributes, which can be useful for certain\n * integration scenarios.\n */\nbody.theme-gym {\n  & > * {\n    --data-theme: gym;\n  }\n}\n\nbody.theme-sports {\n  & > * {\n    --data-theme: sports;\n  }\n}\n\nbody.theme-wellness {\n  & > * {\n    --data-theme: wellness;\n  }\n}\n\nbody.theme-nutrition {\n  & > * {\n    --data-theme: nutrition;\n  }\n}","/* Reset CSS for React App */\n#athlete-dashboard-root {\n  /* Reset box model */\n  box-sizing: border-box;\n  \n  /* Reset margins and padding */\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  \n  /* Ensure our app fills the container */\n  display: block;\n  \n  /* Reset font settings */\n  font-family: var(--font-family-sans);\n  font-size: var(--font-size-base);\n  line-height: 1.5;\n  \n  /* Reset text styles */\n  text-align: left;\n  text-decoration: none;\n  \n  /* Prevent WordPress admin bar from affecting layout */\n  position: relative;\n  z-index: 1;\n  \n  /* Reset all elements inside */\n  & * {\n    box-sizing: border-box;\n  }\n  \n  /* Remove default margins from typography elements */\n  & h1, & h2, & h3, & h4, & h5, & h6, & p, & ul, & ol, & li {\n    margin: 0;\n    padding: 0;\n  }\n  \n  /* Reset list styles */\n  & ul, & ol {\n    list-style: none;\n  }\n  \n  /* Reset links */\n  & a {\n    text-decoration: none;\n    color: inherit;\n    \n    &:hover, &:focus, &:active {\n      outline: none;\n    }\n  }\n  \n  /* Reset buttons */\n  & button {\n    background: none;\n    border: none;\n    padding: 0;\n    cursor: pointer;\n    font-family: inherit;\n    font-size: inherit;\n  }\n  \n  /* Reset images */\n  & img {\n    max-width: 100%;\n    height: auto;\n    display: block;\n  }\n} ","@keyframes float {\n  0% { transform: translate(0, 0) rotate(0deg); }\n  50% { transform: translate(5px, -10px) rotate(5deg); }\n  100% { transform: translate(0, 0) rotate(0deg); }\n}\n\n@keyframes pulse {\n  0%, 100% { opacity: 0.5; }\n  50% { opacity: 0.8; }\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.animate-float {\n  animation: float 10s ease-in-out infinite;\n}\n\n.animate-pulse {\n  animation: pulse 4s cubic-bezier(0.4, 0, 0.6, 1) infinite;\n} ","// Global styles\n// Contains minimal styling essential for application structure\n// Component-specific styles should be in component-level files\n\n// Import all required modules first\n@use './design-system' as ds;\n@use './design-system/tokens/theme-tokens' as tokens;\n@use './theme.scss' as theme;\n@use './reset.scss' as reset;\n@use './animations.scss' as animations;\n@use './mixins.scss' as mixins;\n\n// Other global styles\nbody {\n    font-family: var(--font-family-sans);\n    color: var(--color-text);\n    background-color: var(--color-background);\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n}\n\n// Reset and base styles\n* {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\n// Basic typography\nh1, h2, h3, h4, h5, h6 {\n    font-family: var(--font-family-heading);\n    font-weight: var(--font-weight-bold);\n    line-height: 1.2;\n    margin-bottom: 0.5em;\n}\n\np {\n    margin-bottom: 1em;\n}\n\na {\n    color: var(--color-primary);\n    text-decoration: none;\n    \n    &:hover {\n        text-decoration: underline;\n    }\n}\n\n// Accessibility\n:focus {\n    outline: 2px solid var(--color-focus-ring, var(--color-primary));\n    outline-offset: 2px;\n}\n\n.sr-only {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    padding: 0;\n    margin: -1px;\n    overflow: hidden;\n    clip: rect(0, 0, 0, 0);\n    white-space: nowrap;\n    border-width: 0;\n}\n\n// Container\n.container {\n    width: 100%;\n    max-width: 1200px;\n    margin: 0 auto;\n    padding: 0 var(--spacing-4);\n}\n\n// Responsive media\nimg, video {\n    max-width: 100%;\n    height: auto;\n}\n\n// Reduced motion preferences\n@media (prefers-reduced-motion: reduce) {\n\n    *,\n    *::before,\n    *::after {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}\n\n// Add demo mode navigation styles\n@keyframes highlight-pulse {\n    0% {\n        box-shadow: 0 0 10px rgba(132, 204, 22, 0.4);\n    }\n\n    50% {\n        box-shadow: 0 0 30px rgba(132, 204, 22, 0.8);\n        border-color: rgba(132, 204, 22, 0.8);\n    }\n\n    100% {\n        box-shadow: 0 0 10px rgba(132, 204, 22, 0.4);\n    }\n}\n\n#demo-nav.highlight-pulse {\n    animation: highlight-pulse 1s ease-in-out;\n}\n\n// Demo mode indicator\n.demo-mode-indicator {\n    position: fixed;\n    bottom: 20px;\n    left: 20px;\n    background-color: rgba(0, 0, 0, 0.8);\n    color: #84cc16;\n    padding: 8px 12px;\n    border-radius: 4px;\n    font-size: 12px;\n    z-index: 9999;\n    border: 1px solid #84cc16;\n    box-shadow: 0 0 10px rgba(132, 204, 22, 0.4);\n}"],"sourceRoot":""}]);
